 top: 2.7rem;\n        max-width: 250px;\n    }\n    .main__client__subtitle {\n        font-size: 1rem;\n        line-height: 1.35rem;\n        span {\n            font-weight: bold;\n        }\n    }\n    article{\n        display: flex;\n        flex-wrap: wrap;\n        justify-content: space-between;\n        max-width: 590px;\n        margin: 1rem auto 0 auto;\n    }\n    .main__client__card {\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n        border: 1px solid var(--color-gray-light);\n        border-radius: 5px;\n        padding: 1.5rem;\n        max-width: 260px;\n        margin: 0 auto 1rem auto;\n        img{\n            width: 99px;\n            height: 96px;\n        }\n    }\n    .main__client__text{\n        text-transform: uppercase;\n        font-size: 1rem;\n        align-self: flex-start;\n        color: var(--color-orange);\n        padding-top: 1.55rem;\n    }\n    .main__client__button{\n        margin: 0 auto;\n    }\n    .main__client__price{\n        display: none;\n    }\n    button{\n        margin-left: 2.5rem;\n        margin-right: 2.5rem;\n    }\n    button:last-of-type{\n        margin-top: 1.92rem;\n    }\n}\n/*section shine*/\n.main__shine{\n    position: relative;\n    padding: 2.2rem 0 16rem 0;\n    .container{\n        display: flex;\n        flex-direction: column;\n    }\n    .main__shine__title,\n    .main__shine__price{\n        text-align: center;\n    }\n    .main__shine__price{\n        background-color: var(--color-orange);\n        display: flex;\n        flex-direction: column;\n        justify-content: center;\n        align-items: center;\n        font-size: 3rem; /*48*/\n        position: absolute;\n        bottom: 0;\n        left: 0;\n        right: 0;\n        padding: 2.2rem 0;\n    }\n    .main__shine__button{\n        text-transform: uppercase;\n        border-radius: 5px;\n        color: var(--color-orange);\n        border: none;\n        padding: 1.4rem 3.6rem;\n        margin: 1.6rem 0.3rem 0 0.3rem;\n        font-weight: bold;\n        cursor: pointer;\n    }\n    /* Fazendo a section shine e organizando os itens */\n}\n/*section accordion*/\n.main__accordion{\n    position: relative;\n    .main__accordion__title{\n        position: absolute;\n        top: 2.66rem;\n        padding-left: 0.56rem;\n        max-width: 220px;\n    }\n    input{\n        display: none;\n    }\n    label{\n        display: flex;\n        cursor: pointer;\n        background-color: var(--color-gray-light);\n        font-size: 1.25rem;\n        padding: 0.7rem 1rem;\n        display: flex;\n        justify-content: space-between;\n    }\n    img{\n        transform: rotate(180deg);\n        height: 0.5rem;\n        margin: auto 0;\n    }\n    .main__accordion__content{\n        height: 0;\n        overflow: hidden;\n        transition: height 0.3s ease-in-out;\n        background-color: var(--color-gray-dark);\n        span{\n            display: none;\n        }\n    }\n    [id^=accordion]:checked ~ .main__accordion__content{\n        height: auto;\n        padding: 1.5rem 2rem;\n        border-top: 1px solid var(--color-orange);\n        border-bottom: 1px solid var(--color-orange);\n    }\n    [id^=accordion]:checked ~ label p{\n        font-weight: bold;\n    }\n    [id^=accordion]:checked ~ label img{\n        transform: rotate(360deg);\n    }\n    button{\n        margin-top: 2rem;\n    }\n}\n/*section team*/\n.main__team{\n    .main__team__title{\n        position: relative;\n        top: 2.6rem;\n        left: 0.5rem;\n    }\n    .main__team__cardGroup{\n        display: flex;\n        flex-wrap: wrap;\n        margin: 0 auto;\n    }\n    .main__team__card{\n        border-radius: 5px;\n        border: 1px solid var(--color-gray-light);\n        padding: 1.5rem;\n        display: flex;\n        flex-direction: column;\n        max-width: 100%;\n        margin-bottom: 1.9rem;\n    }\n    .main__team__text{\n        text-transform: uppercase;\n        font-size: 1rem;\n        color: var(--color-orange);\n        padding: 1.5rem 0 0.5rem 0;\n    }\n    .main__team__subtext{\n        padding-right: 1rem;\n    }\n    img{\n        width: 100%;\n    }\n    .main__team__group{\n        display: flex;\n        flex-wrap: wrap;\n        justify-content: center;\n    }\n    .main__team__btnOne{\n        margin-bottom: 1.9rem;\n    }\n}\n/*section feedback*/\n.main__feedback{\n    padding: 3.7rem 0 2.7rem 0;\n    .main__feedback__item{\n        display: none;\n        flex-direction: column;\n        justify-content: center;\n        align-items: flex-end;\n    }\n    .main__feedback__content{\n        text-align: center;\n        font-size: 3rem;\n        font-weight: bold;\n        padding-bottom: 1.5rem;\n    }\n    .main__feedback__name,\n    .main__feedback__description{\n        text-transform: uppercase;\n        color: var(--color-orange);\n        font-size: 1.25rem;\n        align-self: center;\n    }\n    .main__feedback__navigation{\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        padding-top: 0.5rem;\n    }\n    .main__feedback__navItem{\n        font-weight: bold;\n        font-size: 1.2rem;\n        min-width: 35px;\n        margin: 10px 0;\n        cursor: pointer;\n        color: var(--color-gray-light);\n    }\n    .main__feedback__navItem:hover:not(.main__feedback--white){\n        width: auto;\n        color: var(--color-white);\n    }\n    .main__feedback--white{\n        color: transparent;\n        height: 4px;\n        width: 50px;\n        background-color: var(--color-white);\n        margin-right: 25px;\n    }\n}\n/*section methodology*/\n.main__methodology{\n    position: relative;\n    .container{\n        padding-top: 2.7rem;\n        display: grid;\n        grid-template-areas:\n            \"titleMethod\"\n            \"listMethod\"\n            \"buttonMethod\";\n    }\n    .square{\n        position: absolute;\n        top: 0;\n    }\n    .main__methodology__title{\n        grid-area: titleMethod;\n        padding: 0 0 0.5rem 0;\n    }\n    .main__methodology__text{\n        max-width: 250px;\n        padding: 0 0 1rem 0.6rem;\n    }\n    .main__methodology__subtext{\n        padding-right: 0.9rem;\n        span{\n            font-weight: bold;\n        }\n    }\n    .main__methodology__list{\n        grid-area: listMethod;\n        padding-top: 2.7rem;\n        padding-bottom: 1rem;\n    }\n    .main__methodology__item{\n        padding-bottom: 2rem;\n        display: flex;\n        flex-wrap: wrap;\n        align-items: center;\n    }\n    .circle{\n        width: 1rem;\n        height: 1rem;\n        border: 1px solid var(--color-orange);\n        border-radius: 50%;\n        margin-right: 0.8rem;\n    }\n    .main__methodology__content{\n        text-transform: uppercase;\n        color: var(--color-orange);\n        font-size: 1rem;\n    }\n    .main__methodology__description{\n        padding: 0.6rem 0 0 1.5rem;\n    }\n    .main__methodology__group{\n        grid-area: buttonMethod;\n        padding-bottom: 0rem;\n    }\n    button{\n        margin-bottom: 2rem;\n    }\n}\n/*section target*/\n.main__target{\n    .container{\n        position: relative;\n    }\n    .square{\n        position: absolute;\n    }\n    .main__target__title{\n        padding: 2.7rem 2rem 1rem 0.6rem;\n    }\n    span{\n        font-weight: bold;\n    }\n    .main__target__group{\n        padding: 2.2rem 0 1.2rem 0;\n    }\n    .main__target__group{\n        padding: 0 auto;\n        display: flex;\n        flex-wrap: wrap;\n        justify-content: center;\n    }\n    button{\n        margin: 1rem 0;\n    }\n}\n\n@media only screen and (min-width: 760px) {\n    .main__home button,\n    .main__client button {\n        font-size: 0.84rem;\n    }\n    .main__methodology .main__methodology__btnOne,\n    .main__target .main__target__btnOne{\n        padding-left: 1.3rem;\n        padding-right: 1.3rem;\n    }\n    .main__methodology .main__methodology__btnTwo,\n    .main__target .main__target__btnTwo{\n        padding-left: 1.6rem;\n        padding-right: 1.6rem;\n    }\n    /*section home*/\n    .main__home {\n        .main__home__title {\n            margin: 0 auto;\n            padding-top: 5.8rem;\n        }\n        .main__home__subtitle {\n            font-size: 1.25rem;\n            margin-bottom: 3.36rem;\n            margin-top: 1.3rem;\n        }\n        .main__home__group {\n            padding-bottom: 7.9rem;\n        }\n    }\n    /*section client*/\n    .main__client {\n        .container{\n            grid-template-areas:\n            \"titleClient buttonClient\"\n            \"cardClient cardClient\";\n        }\n        .main__client__group {\n            width: 450px;\n        }\n        .main__client__title {\n            padding-left: 3.2rem;\n            top: 5.6rem;\n        }\n        span {\n            display: inline-block;\n        }\n        .main__client__subtitle {\n            font-size: 1.25rem;\n            padding-left: 3.2rem;\n            line-height: 1.625rem;\n        }\n        article {\n            margin-top: 5rem;\n            margin-bottom: 2.8rem;\n        }\n        .main__client__card {\n            margin-bottom: 2.15rem;\n        }\n        .main__client__button {\n            justify-content: start;\n            flex-direction: column;\n        }\n        .main__client__price {\n            display: flex;\n            align-self: end;\n            padding: 7.25rem 5.75rem 0.4rem 0;\n        }\n        button {\n            margin: 0;\n        }\n        .main__client__btnOne {\n            padding: 1.4rem 1.3rem;\n        }\n        .main__client__btnTwo {\n            padding: 1.4rem 1.5rem;\n        }\n    }\n    /*section shine*/\n    .main__shine {\n        padding: 4rem 0;\n        .main__shine__title h2 {\n            max-width: 387px;\n        }\n        .main__shine__price {\n            right: 0;\n            bottom: 0;\n            left: auto;\n            top: 0;\n            margin: 2.5rem 0;\n            padding: 0 2.15rem;\n        }\n        .main__shine__button {\n            padding: 1.4rem 3.1rem;\n        }\n    }\n    /*section accordion*/\n    .main__accordion {\n        padding: 3.8rem 0;\n        .container{\n            position: relative;\n        }\n        .square,\n        .main__accordion__title {\n            display: none;\n        }\n        article{\n            display: flex;\n        }\n        label{\n            padding: 0;\n            position: relative;\n            img{\n                display: none;\n            }\n        }\n        label p{\n            padding: 1.66rem 1rem;\n            font-size: 2rem;\n            width: 230px;\n        }\n        label::after{\n            content: '';\n            width: 92%;\n            height: 1px;\n            background-color: var(--color-orange);\n            position: absolute;\n            bottom: 0.01rem;\n            right: 0;\n            visibility: hidden;\n        }\n        .main__accordion__content{\n            width: calc(100% - 245px);\n            position: absolute;\n            right: 1rem;\n            bottom: 0;\n            top: 0;\n            font-size: 1.25rem;\n            z-index: 1;\n            span{\n                display: inline;\n                font-weight: bold;\n            }\n        }\n        [id^=accordion]:checked ~ .main__accordion__content{\n            padding: 4rem 2rem 7.5rem 2rem;\n            border: none;\n            overflow: auto;\n        }\n        // scrow no conteúdo do section main__accordion\n        [id^=accordion]:checked ~ label::after{\n            visibility: visible;\n        }\n        .main__accordion__group{\n            position: absolute;\n            bottom: 0;\n            right: 2.2rem;\n            left: 230px;\n            padding-right: 1rem;\n            padding-bottom: 4.2rem;\n            z-index: 1;\n            background-color: var(--color-gray-dark);\n            justify-content: end;\n        }\n        .main__accordion__group button{\n            border: none;\n            background-color: transparent;\n            font-size: 1.04rem;\n            font-weight: bold;\n            padding: 0 1rem;\n        }\n        .main__accordion__btnTwo{\n            color: var(--color-orange);\n        }\n    }\n    /*section team*/\n    .main__team{\n        width: 100%;\n        .main__team__title{\n            top: 5.4rem;\n            left: 3.2rem;\n        }\n        .main__team__cardGroup{\n            justify-content: space-between;\n            margin: 0 0 1.3rem 0;\n        }\n        .main__team__card{\n            margin-bottom: 1.2rem;\n            max-width: 333px;\n        }\n        .main__team__subtext{\n            padding-right: 0.5rem;\n        }\n        .main__team__group{\n            padding: 1.3rem 0 5rem 0;\n        }\n        .main__team__btnOne{\n            padding: 1.3rem;\n            margin-right: 2rem;\n            margin-bottom: 0;\n        }\n        .main__team__btnTwo{\n            padding: 1.4rem 1.6rem;\n        }\n    }\n    /*section feedback*/\n    .main__feedback {\n        padding: 2.5rem 0;\n        .container{\n            position: relative;\n        }\n        .lineTop,\n        .lineBottom {\n            width: 80px;\n            height: 80px;\n            position: absolute;\n            border-top: 1px solid var(--color-orange);\n            border-right: 1px solid var(--color-orange);\n        }\n        .lineTop{\n            right: 1rem;\n        }\n        .lineBottom{\n            left: 1rem;\n            transform: rotate(180deg);\n            bottom: 0;\n        }\n        .main__feedback__content,\n        .main__feedback__name,\n        .main__feedback__description{\n            padding: 0 5rem;\n        }\n        .main__feedback__name,\n        .main__feedback__description{\n            align-self: flex-end;\n        }\n        .main__feedback__content{\n            padding-top: 3.8rem;\n            padding-bottom: 2rem;\n        }\n        .main__feedback__navigation{\n            padding: 3.8rem 0 2.5rem 0;\n        }\n        .main__feedback__navItem{\n            font-size: 1.8rem;\n        }\n        .main__feedback__navItem:hover{\n            width: 60px;\n        }\n        .main__feedback__left,\n        .main__feedback__right{\n            width: 25px;\n            height: 25px;\n            cursor: pointer;\n            position: absolute;\n            top: 50%;\n            border-bottom: 5px solid var(--color-gray-light);\n            border-left: 5px solid var(--color-gray-light);\n            border-bottom-left-radius: 3px;\n        }\n        .main__feedback__left{\n            position: absolute;\n            left: 1.5rem;\n            transform: rotate(50deg);\n        }\n        .main__feedback__right{\n            position: absolute;\n            right: 1.5rem;\n            transform: rotate(-135deg);\n        }\n        .main__feedback__navItem{\n            min-width: 45px;\n        }\n        .main__feedback--white{\n            width: 60px;\n        }\n    }\n    /*section methodology*/\n    .main__methodology{\n        order: 1;\n        .container{\n            padding-top: 5.4rem;\n            display: grid;\n            grid-template-areas:\n                \"titleMethod titleMethod buttonMethod\"\n                \"titleMethod titleMethod .\"\n                \"listMethod listMethod listMethod\";\n        }\n        .main__methodology__title{\n            padding-left: 3.2rem;\n        }\n        .main__methodology__text{\n            max-width: 260px;\n            padding: 0 0 1rem 0rem;\n        }\n        .main__methodology__list{\n            padding-top: 4.5rem;\n            padding-bottom: 2.9rem;\n        }\n        .main__methodology__subtext{\n            font-size: 1.25rem;\n        }\n        .main__methodology__description{\n            padding: